const HtmlAttributes = Vue.extend({
  name: 'HtmlAttributes',
  data: function () {
    return {
      state: {
        attributesStyle: ''
      } 
    }
  },

  created() {
    if (window.screen.width <= 414) {
      this.state.attributesStyle = 'h-12'
    } else {
      this.state.attributesStyle = 'h-10'
    }
  },
  methods: {
    handleRouteTo(urlPath: string) {
      this.$router.push({
        path: urlPath
      })
    }
  },
  watch: {
    $route(to, from) {
      // react to route changes...
      console.log({to, from})
      
    }
  }
})

export default HtmlAttributes

